.navbar {
    background-color: #333;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.link {
    color: #fff;
    text-decoration: none;
    margin: 5px;
}

.login-button {
    background-color: #fff;
    color: #333;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
}

.login-button:hover {
    background-color: #ddd;
}

.logout-button {
    margin: 5px;
    background-color: #ffb0b0;
    color: #333;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
}

.logout-button:hover {
    background-color: #ff3737;
    color: white;
}

/* ======================================================================================================== */

.home-div {
    background-color: #fff7bc;
    border: 1px solid #857c7c;
    border-radius: 8px;
    box-shadow: 0 0 20px rgb(66, 65, 65);
    padding: 20px;
    margin-top: 20px;
}

.home-div h1 {
    font-size: 24px;
    margin-bottom: 10px;
}

.home-div ol {
    padding-left: 20px;
}

.home-div li {
    font-size: 18px;
    margin-bottom: 10px;
}


/* ======================================================================================================== */

.form-div {
    margin-top: 20px;
    background-color: #f9f9f9;
    padding: 20px;
    padding-right: 50px;
    border-radius: 8px;
    box-shadow: 0 0 20px #0044ff;
}

.form-input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form-input:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 10px #007bff;
}

.form-next-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 10px;
    margin: 20px;
    border-radius: 4px;
    cursor: pointer;
}

.form-end-button {
    background-color: #ff0000;
    color: #fff;
    border: none;
    padding: 10px 10px;
    margin: 20px;
    border-radius: 4px;
    cursor: pointer;
}

.form-end-button:hover {
    background-color: #7d0303;
}

.form-next-button:hover {
    background-color: #054386;
}

/* ======================================================================================================== */

.history-div {
    margin-top: 20px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgb(66, 65, 65);
    overflow-x: auto;
}

.history-table {
    width: 100%;
}

.history-table-header,
.history-table-data {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    text-align: center;
}

.history-table-row:hover {
    background-color: #f0f0f0;
}

.history-table-header {
    font-weight: bold;
    background-color: #007bff;
    color: #fff;
}

.history-table-head-button {
    background-color: #00ccff;
    color: #000000;
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0 5px #0044ff;
}

.history-table-delete-button {
    background-color: #ff0000;
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0 5px #ff0000;
}

.history-table-stud-button {
    background-color: #00ccff;
    color: #000000;
    border: none;
    padding: 5px 10px;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0 5px #0044ff;
}

.history-table-data button:hover {
    background-color: #0056b3;
    color: white;
}

